<template>
    <section data-v-portal>
        <div id="appTitle" data-v-portal>{{appTitle}}</div>
    </section>
</template>

<script>
export default {
    name : 'Portal',
    data(){
        return{
            welcomeMsg : "Welcome to",
            appTitle : "aliss"
        }
    }
}
</script>

<style scoped>
    section[data-v-portal]{
        height:250px;
    }

    h1[data-v-portal]{
        margin : 5px 0px;
    }

    #appTitle[data-v-portal]{
        font-size:400%;
        font-family: 'Didact Gothic';
        text-align: center;
        color: #f16272;

        padding :10% 0px;
    }
</style>



