<template>
    <footer data-v-footer>
        <p>{{footerTitle}} ver {{version}}</p>
    </footer>
</template>

<script>
export default {
    name : 'FooterComp',
    data(){
        return{
            footerTitle : "aliss WEB Suite",
            version : "1.0.0"
        }
    }
}
</script>

<style scoped>
footer[data-v-footer] {
    background: #fafafa;
    font-family: 'Didact Gothic';
    font-size: 1em;
    text-align: center;
    padding : 1px 0px;
    margin : 10px 0px;
}

footer a[data-v-footer] {
  color: #8c8c8c;
  text-decoration: none;
  transition: border-color 0.3s;
}

footer a:hover[data-v-footer] {
  border-bottom: 1px dotted #8c8c8c;
}
</style>



