<template>
    <transition name="sidef">
        <div style="position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 200%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        display: table;">
        
            <div id="menu" data-v-sidemenuF>
                <div class="buttonBase" style="margin:20px 0px" v-on:click="closeMenu">Close</div>

                <span v-for="(menu, index) in menuList" :key="index">
                    <h1 v-if = "menu.layer==1 && menu.select && !menu.del" class="selected" data-v-sidemenuF>
                        {{ menu.pageName }}
                    </h1>
                    <h1 v-if = "menu.layer==1 && !menu.select && !menu.del" data-v-sidemenuF>
                        <template v-if= 'menu.url != null'>
                            <a v-bind:href = "menu.url" v-if= 'menu.url != null'>
                                {{ menu.pageName }}
                            </a>
                        </template>
                        <template v-else>
                            {{ menu.pageName }}
                        </template>
                    </h1>
                    <h2 v-if = "menu.layer==2 && menu.select && !menu.del" class="selected" data-v-sidemenuF>
                        {{ menu.pageName }}
                    </h2>
                    <h2 v-if = "menu.layer==2 && !menu.select  && !menu.del" data-v-sidemenuF>
                        <template v-if= 'menu.url != null'>
                            <a v-bind:href = "menu.url" v-if= 'menu.url != null'>
                                {{ menu.pageName }}
                            </a>
                        </template>
                        <template v-else>
                            {{ menu.pageName }}
                        </template>
                    </h2>
                </span>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name : 'SideMenu',
    data: function () {
        return {
            title: "test",
            menuList: [
                {
                    id: 1,
                    pageName: "Portal",
                    url: location.pathname + "?pId=portal",
                    layer: "1",
                    select: false,
                    del: false
                },
                {
                    id: 7,
                    pageName: "Financial Accountings",
                    url: null,
                    layer: "1",
                    select: false,
                    del: false
                },
                {
                    id: 8,
                    pageName: "家計簿入力",
                    url: location.pathname + "?pId=fa01",
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 9,
                    pageName: "Bank間振替",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 10,
                    pageName: "月次締処理",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 14,
                    pageName: "WishList",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 11,
                    pageName: "Bank設定",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 12,
                    pageName: "Account階層設定",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 13,
                    pageName: "残高照会",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 2,
                    pageName: "User Management",
                    url: null,
                    layer: "1",
                    select: false,
                    del: false
                },
                {
                    id: 3,
                    pageName: "個人設定",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 4,
                    pageName: "UM設定(Admin)",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 5,
                    pageName: "Role設定(Admin)",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                },
                {
                    id: 6,
                    pageName: "Auth設定(Admin)",
                    url: null,
                    layer: "2",
                    select: false,
                    del: false
                }
            ]
        }
    },
    methods : {
        closeMenu(){
            this.$emit('menu-close-event')
        }
    }
}
</script>

<style scoped>
    #menu[data-v-sidemenuF]{
        /* margin : 15px; */
        background-color: #fafafa;
        height:100%;
        width:250px;

        /* margin-top:-10px; */
        padding:10px 15px;
    }

    #menu h1[data-v-sidemenuF]{
        font-size: 120%;
        font-weight: 500;
        margin: 10px 0px 5px 0px;
    }

    #menu h2[data-v-sidemenuF]{
        font-size: 110%;
        font-weight: 300;
        margin: 5px 0px 5px 10px;
    }

    #menu .selected[data-v-sidemenuF]{
        color: #f16272;
    }

    .sidef-enter-active, .sidef-leave-active{
        transition: opacity 0.5s, transform 0.5s;
    }
    .sidef-enter{
        opacity: 0;
        transform : translatex(-250px);
    }
    .sidef-leave-to{
        opacity: 0;
        transform : translatex(-250px);
    }


</style>





