<template>
    <div class="login-mask" data-v-login>
        <div class="form-wrapper" data-v-login>
            <h1 data-v-login>Sign In</h1>
            <form data-v-login>
                <div class="form-item" data-v-login>
                    <label for="id"></label>
                    <input type="text" name="id" required="required" placeholder="Login ID" v-model="loginId" data-v-login />
                </div>
                <div class="form-item" data-v-login>
                    <label for="password"></label>
                    <input type="password" name="password" required="required" placeholder="Password" v-model="passWord" v-on:keyup.enter="login" data-v-login />
                </div>
                <div class="button-panel" data-v-login>
                    <input type="button" class="button" title="Sign In" value="Sign In" v-on:click="login" data-v-login />
                </div>
            </form>
            <div class="form-footer" data-v-login>
                <p>aliss WEB Suite</p>
            </div>
        </div>
        <Modal
            v-show="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "loginModalOk"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>
        <Loading v-show="loading" />
    </div>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import Modal    from '../components/Modal'
import Loading  from '../components/Loading'


export default {
    name: "Login",

    components : {
        Modal,
        Loading
    },

    data(){
        return {
            loginId     : '',
            passWord    : '',

            showModal   : false,
            modalMsg    : '',

            loading     : false,

            apiUrls : {
                login : "https://aliss-app.herokuapp.com/api/v1/auth"
                // login : "http://localhost:3000/api/v1/auth"
            }
        }
    },

    methods:{
        // ログイン処理
        login (){
            this.loading = true
            const self   = this

            // ログインAPI call
            axios.post(this.apiUrls["login"], 
                {
                    userId : this.loginId,
                    password : this.passWord
                }
            )
            .then(function(response){
                // login success
                if(response.status == 200)
                {
                    self.loading = false
                    Cookies.set('token', response.data.token, { expires: 0.5 });
                    location.reload()
                    window.scrollTo(0,0)
                    return
                }
                // login failed
                self.loading    = false
                self.modalMsg   = 'Login ID、パスワードが正しくありません。'
                self.showModal  = true
            })
            .catch(function(error){
                self.loading = false
                // aliss service not response
                self.modalMsg   = 'alissサービスに接続できません。<br>時間をおいてから再度お試しください。'
                self.showModal  = true
            })
        },

        // ログイン画面モーダルOK押下
        loginModalOk (){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    /* Fonts */
    @import url(https://fonts.googleapis.com/css?family=Open+Sans:400);
    /* @import url(http://weloveiconfonts.com/api/?family=fontawesome); */
    @import url(https://fonts.googleapis.com/css?family=Didact+Gothic);
    .login-mask[data-v-login] {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color:#e9e9e9;
        color: #5e5e5e;
        font: 400 87.5%/1.5em 'Open Sans', sans-serif;
        /* display: table; */
        /* transition: opacity .3s ease; */
    }
    .form-wrapper[data-v-login] {
        background: #fafafa;
        margin: 3em auto;
        padding: 0 1em;
        max-width: 370px;
        width : 90%;
    }

    h1[data-v-login] {
        text-align: center;
        padding: 1em 0;
    }

    form[data-v-login] {
        padding: 0 1.5em;
    }

    .form-item[data-v-login] {
        margin-bottom: 0.75em;
        width: 100%;
    }

    .form-item input[data-v-login] {
        background: #fafafa;
        border: none;
        border-bottom: 2px solid #e9e9e9;
        color: #666;
        font-family: 'Open Sans', sans-serif;
        font-size: 1em;
        height: 50px;
        transition: border-color 0.3s;
        width: 100%;
    }

    .form-item input:focus[data-v-login] {
        border-bottom: 2px solid #c0c0c0;
        outline: none;
    }

    .button-panel[data-v-login] {
        margin: 2em 0 0;
        width: 100%;
    }

    .button-panel .button[data-v-login] {
        background: #f16272;
        border: none;
        color: #fff;
        cursor: pointer;
        height: 50px;
        font-family: 'Open Sans', sans-serif;
        font-size: 1.2em;
        letter-spacing: 0.05em;
        text-align: center;
        text-transform: uppercase;
        transition: background 0.3s ease-in-out;
        width: 100%;
    }

    .button:hover[data-v-login] {
        background: #ee3e52;
    }

    .form-footer[data-v-login] {
        font-family: 'Didact Gothic';
        font-size: 1em;
        padding: 2em 0;
        text-align: center;
    }

    .form-footer a[data-v-login] {
        color: #8c8c8c;
        text-decoration: none;
        transition: border-color 0.3s;
    }

    .form-footer a:hover[data-v-login] {
        border-bottom: 1px dotted #8c8c8c;
    }

</style>



