<template>
    <p data-v-viewheader>{{moduleName}} / {{viewName}}</p>
</template>

<script>
export default {
    name : 'ViewHeader',

    props : ["moduleName", "viewName"]

}
</script>

<style scoped>
    p[data-v-viewheader]{
        color: #f16272;
        font-size:120%;

        margin:0px 0px 15px 0px;
    }
</style>





