<template>
    <div>
        <header style="overflow: hidden;">
            <div id="headerTitle">
                <div id="mnavToggle" v-on:click = "clickToggle">
                    <div>
                        <span />
                        <span />
                        <span />
                    </div>
                </div>
                <h1>aliss</h1>
            </div>
            <div id="headerUser">
                <p><span id="userIcon">●</span> {{ userName }}</p>
                <p><a href="#" v-on:click="singOut">Sign Out</a></p>
            </div>
            <div id="subTitle">
                <span style="color:#f16272;">A</span>ctivity and
                <span style="color:#f16272;">LI</span>felog
                <span style="color:#f16272;">S</span>tored
                <span style="color:#f16272;">S</span>ervice
            </div>
        </header>
        <Modal
            v-show="showModal"
            v-bind:mtypeIn='1'
            v-on:modal-yes-event = "confirmYes"
            v-on:modal-no-event  = "confirmNo"
        >
            <p slot="header">aliss Confirmation</p>
            <template slot="body">
                alissからサインアウトしますか？
            </template>
        </Modal>
    </div>
</template>

<script>
import Modal from '../components/Modal'
import axios    from 'axios';
import Cookies  from 'js-cookie';

export default {
    name : 'TitleHeader',

    components : {
        Modal
    },

    data(){
        return {
            showModal : false,
            apiUrls : {
                logout : "https://aliss-app.herokuapp.com/api/v1/user/logout"
                // authToken : "http://localhost:3000/api/v1/user/logout"
            },
            
        }
    },

    props: ['userName'],

    methods: {
        clickToggle (){
            this.$emit('toggle-clicked-event')
        },

        // サインアウト押下
        singOut(){
            this.showModal = true;
        },

        // サインアウトyes
        confirmYes (){
            let token = Cookies.get('token')

            // ログアウトAPI
            axios.get(this.apiUrls["logout"],
            {
                headers : {
                    'Authorization' : token
                }
            })

            // クッキー削除
            Cookies.remove('token');

            // モーダル閉じる
            this.showModal = false

            // リロード
            location.reload()
        },

        // サインアウトno
        confirmNo (){
            this.showModal = false
        }
    },
}
</script>

<style scoped>
    header{
        background: #fafafa;
        margin:0px;
        padding:10px 0px 20px 0px;

        margin:0 auto;
        margin-top:10px;
    }
    header h1{
        font-family: 'Didact Gothic';
        font-size: 250%;
        font-weight: 300;
        margin: 0px 0px 0px 20px;
    }
    header p{
        font-family: 'Didact Gothic';
        margin: 3px 0px 0px 25px;
    }

    #headerTitle{
        float:left;
        width:50%;
    }
    #headerUser{
        min-width:80px;
        float:right;
        margin:5px 15px 10px 0px;
    }
    #headerUser a{
        font-size:120%;
        color: #f16272;
        border-bottom: 1px solid #f16272;
        margin-left:1rem;
    }
    #headerUser a:hover{
        color: #dcdcdc;
        text-decoration: none;
        border-bottom: 1px solid #dcdcdc;
    }

    #subTitle{
        clear:both;
        margin: -50px 0px 0px 75px;
        padding: 0px;
    }
    
    #userIcon{
        color: #f16272;
    }

    #mnavToggle {
        width: 45px;
        height: 25px;
        cursor: pointer;
        float: left;
        margin: 12px 15px 25px 15px;
    }
    #mnavToggle span {
        display: block;
        position: absolute;
        width: 100%;
        border-bottom: solid 5px #f16272;
        -webkit-transition: .35s ease-in-out;
        -moz-transition: .35s ease-in-out;
        transition: .35s ease-in-out;
    }
    #mnavToggle span:nth-child(1) {
        top: 0;
    }
    #mnavToggle span:nth-child(2) {
        top: 15px;
    }
    #mnavToggle span:nth-child(3) {
        top: 30px;
    }
    #mnavToggle div {
        position: relative;
    }
</style>




