<template>
  <div id="app">
    <!-- ハンバーガーメニューコンポーネント -->
    <SideMenuF v-show="side" v-on:menu-close-event = "closeToggle"/>
    
    <!-- タイトルコンポーネント -->
    <TitleHeader
        v-on:toggle-clicked-event = "clickToggle"
        v-bind:userName="userInfo.name"
    />

    <!-- メイン画面 -->
    <div id="mainView">
        <div id="mainComp">
            <!-- ポータルコンポーネント -->
            <Portal v-show="this.pageId.portal" />

            <!-- 家計簿入力コンポーネント -->
            <FAinput v-show="this.pageId.fa01"/>
        </div>
        
        <!-- フッターコンポーネント -->
        <FooterComp/>
    </div>

    <!-- ログインコンポーネント -->
    <Login v-show="!this.login"/>

    <!-- モーダル -->
    <Modal
        v-show="showModal"
        v-bind:mtypeIn='2'
        v-on:modal-ok-event    = "appModalOk"
    >
        <p slot="header">aliss Confirmation</p>
        <template slot="body">
            <p v-html="modalMsg" />
        </template>
    </Modal>

    <Loading v-show="loading" />

  </div>
</template>

<script>
import axios    from 'axios';
import Cookies  from 'js-cookie';

import HelloWorld   from './components/HelloWorld'
import TitleHeader  from './components/TitleHeader'
import SideMenu     from './components/SideMenu'
import SideMenuF    from './components/SideMenuFloat'
import FooterComp   from './components/Footer'

import Portal       from './components/Portal'
import FAinput      from './components/FAinput'

import Login        from './components/Login'
import Modal        from './components/Modal'
import Loading      from './components/Loading'

export default {
    name: 'App',
    components: {
      HelloWorld,
      TitleHeader,
      SideMenu,
      SideMenuF,
      FooterComp,
      Portal,
      FAinput,
      Login,
      Modal,
      Loading
    },

    data(){
      return {
        // ページID一覧
        pageId : {
            portal  : false,
            fa01    : false
        },

        // ハンバーガーメニュー表示制御
        side : false,

        //  ログインコンポーネント表示制御
        login : true,

        // ローディング画面
        loading : false,

        // モーダル表示制御
        showModal : false,
        // モーダルメッセージ
        modalMsg  : '',

        apiUrls : {
            authToken : "https://aliss-app.herokuapp.com/api/v1/user"
            // authToken : "http://localhost:3000/api/v1/user"
        },

        // ユーザー情報
        userInfo : {
            userId : "",
            name   : ""
        }

      }
    },

    methods : {
        // ハンバーガーメニュー展開
        clickToggle(){
            this.side = true
        },
        // ハンバーガーメニュー閉じる
        closeToggle(){
            this.side = false
        },
        // モーダルOK押下
        appModalOk (){
            this.showModal = false
        }
    },

    // 画面初期化処理
    created: function(){
        // clear Page
        for(let key in this.pageId){
            this.pageId[key] = false
        }

        // check login token
        let token = Cookies.get('token')
        if(token == null)
        {
            this.login = false
            return
        }

        this.loading = true

        // thisを格納
        const self = this

        // Toke Auth API call
        axios.get(this.apiUrls["authToken"],
        {
            headers : {
                'Authorization' : token
            }
        })
        .then(function(response){
            // Http status not 200
            if(response.status != 200)
            {
                self.loading = false
                self.login   = false
                return
            }
            // Auth failed
            if(response.data.status != 'S')
            {
                self.loading = false
                self.login   = false
                return
            }
            // Auth success
            self.login = true
            self.loading = false

            self.userInfo.userId = response.data.data.userId
            self.userInfo.name   = response.data.data.name
        })
        .catch(function(error){
            // aliss service not response
            self.modalMsg   = 'alissサービスに接続できません。<br>時間をおいてから再度お試しください。'
            self.showModal  = true
            self.loading    = false
            self.login      = false
            return
        })

        // get URL Param
        let urlParams = new URLSearchParams(window.location.search)
        let pId = urlParams.get('pId')

        if(this.pageId[pId] == null)
        {
            this.pageId['portal'] = true
            return
        }

        this.pageId[pId] = true

    }
}
</script>

<style>
    @import url(https://fonts.googleapis.com/earlyaccess/roundedmplus1c.css);
    @import url(https://fonts.googleapis.com/css?family=Open+Sans:400);
    /* @import url(http://weloveiconfonts.com/api/?family=fontawesome); */
    @import url(https://fonts.googleapis.com/css?family=Didact+Gothic);

    body{
      background: #e9e9e9;
      height:100%;
    }

    #app {
        font-family: 'Didact Gothic', 'Open Sans','Rounded Mplus 1c', Segoe UI, "メイリオ",  Meiryo, sans-serif;
        font-weight:300;
        background: #e9e9e9;
        color: #5e5e5e;
        margin: 0 auto;

        max-height: 100%;
        -webkit-text-size-adjust: 100%;
    }

    header{
        background: #fafafa;
        margin:0px;
        padding:10px 0px 20px 0px;

        /* width:95%;
        max-width:950px; */
        margin:0 auto;
        margin-top:10px;
    }
    header h1{
        font-family: 'Didact Gothic';

        font-size: 250%;
        font-weight: 300;
        margin: 0px 0px 0px 20px;
    }
    header p{
        font-family: 'Didact Gothic';
        margin: 3px 0px 0px 25px;
    }

    #app{
        width:95%;
        max-width:950px;
        margin:0 auto;
    }

    #sideBar{
        background: #fafafa;
        width: 30%;
        /* width:250px; */
        /* min-width: 250px; */
        margin-top: 10px;
        margin-right: 2%;
        float:left;

        /* position: fixed;
        z-index: 1; */
    }

    #mainView{
      /* width : 68%; */

      width : 100%;
      margin-top: 10px;
      float:left;
      z-index: 0;
    }

    #mainComp{
      background: #fafafa;
      margin : 0px 0px 10px 0px;
      padding : 20px 20px;
    }

    .buttonBase {
      background: #f16272;
      border: none;
      color: #fff;
      cursor: pointer;
      /* height: 50px; */
      /* font-family: 'Open Sans', 'Rounded Mplus 1c', sans-serif; */
      font-size: 1em;
      letter-spacing: 0.05em;
      text-align: center;
      text-transform: uppercase;
      transition: background 0.3s ease-in-out;
      /* width: 100%; */
      padding : 5px;
    }

    .buttonBase:hover {
        background: #ee3e52;
    }

    /* for mobile 710px以下 */
    /* @media(max-width:710px){
        #sideBar{
            display: none;
        }
        #mainView{
            width: 100%;
        }
    } */

    input {
        background: #fafafa;
        border: none;
        border-bottom: 2px solid #e9e9e9;
        color: #666;
        font-family: 'Open Sans', sans-serif;
        font-size: 1em;
        height: 50px;
        transition: border-color 0.3s;
        width: 100%;
    }
    input:focus {
        border-bottom: 2px solid #c0c0c0;
        outline: none;
    }
    select{
        background: #fafafa;
        border: none;
        border-bottom: 2px solid #e9e9e9;
        color: #666;
        font-family: 'Open Sans', sans-serif;
        font-size: 1em;
        width: 180px;
        height: 50px;

        margin-top: 4px;

        transition: border-color 0.3s;
    }
    select:focus {
        border-bottom: 2px solid #c0c0c0;
        outline: none;
    }

    a{
        color: #5e5e5e;
        text-decoration: none;
        border-bottom: 1px solid #999999;
        transition-property: all;
        transition: 0.3s ease-out;
    }

    a:hover{
        color: #dcdcdc;
        text-decoration: none;
        border-bottom: 1px solid #dcdcdc;
    }
</style>



